<script setup>
    import { onMounted, ref } from 'vue';
    import Button from './../../components/Button';
    import post from '../../utils/post';
    import store from '../../store';
    import formatMoney from '../../utils/formatMoney';

    const selectedTax = ref(0);

    function selectTaxHandle(index){
        selectedTax.value = index;
    }

    async function payTax(){
        if(store.state.initial.balance >= store.state.initial.Taxes[selectedTax.value].value){
            const res = await post('TaxPayment', { id: store.state.initial.Taxes[selectedTax.value].id });
            if(res){
                store.state.initial.Taxes.splice(selectedTax.value, 1);
                selectedTax.value = 0;
            }
        }
    }
</script>

<template>
    <div class="alert">
        <p>Impostos</p>
        <div class="main-alert">
            <div v-if="store.state.initial.Taxes?.length === 0" class="not-fines">
                <img src="./../../assets/images/not.svg" />
                <p>Não há impostos para pagar!</p>
            </div>
            <div class="list-container" v-if="store.state.initial.Taxes?.length > 0">
                <div class="list-frame">
                    <div class="item-alert" :class="{'selected': selectedTax === index}" v-for="(item,index) in store.state.initial.Taxes" :key="index">
                        <p class="description-name">
                            <span class="id">#{{index + 1}}</span>
                            {{item.id}}
                        </p>
                        <p class="type-name">{{item.name}}</p>
                        <p class="value">{{formatMoney(item.value)}}</p>
                        <Button class="button" v-show="selectedTax !== index" :cb="selectTaxHandle" :args="[index]" text="ver" />
                    </div>
                </div>
            </div>
            <div class="description-frame" v-if="store.state.initial.Taxes?.length > 0">
                <div class="header-description">
                    <div class="temp-frame">
                        <span>Data</span>
                        <p>{{store.state.initial.Taxes[selectedTax]?.date}}</p>
                    </div>
                    <div class="temp-frame">
                        <span>Hora</span>
                        <p>{{store.state.initial.Taxes[selectedTax]?.hour}}</p>
                    </div>
                </div>
                <div class="description-container">
                    <h4>Descrição</h4>
                    <div class="description-text">
                        {{store.state.initial.Taxes[selectedTax]?.message}}
                    </div>
                </div>
                <div class="risk"></div>
                <div class="payment-frame">
                    <p>TOTAL</p>
                    <span>{{formatMoney(store.state.initial.Taxes[selectedTax]?.value)}}</span>
                    <button @click="payTax">PAGAR</button>
                </div>
            </div>
        </div>
    </div>
</template>

